import React from 'react';
import CommonTable from '../CommonTable'

const columns = [
  { id: 'EquipName', label: '', minWidth: 170 },
  { id: 'DateTime', label: '', minWidth: 100 },
  { id: 'Name', label: '', minWidth: 100 },
  { id: 'Projection', label: '', minWidth: 100 },
  { id: 'Direction', label: '', minWidth: 100 },
  { id: 'AgeId', label: '. ', minWidth: 100 },
  { id: 'Constitution', label: '', minWidth: 100 },  
];

export default function OrganAutoTable(props) {
  console.log("render OrganAutoTable");

  const rows = props.data;
  return (
    <CommonTable columns={columns} rows={rows}></CommonTable>
  );
}