import React from 'react';
import CommonTable from '../CommonTable'
import {parseLocalString} from '../../../utilities/utils'

const columns = [
    { id: 'EquipName', label: '', minWidth: 170 },
    { id: 'DateTime', label: '', minWidth: 100,
      format: (value) => parseLocalString(value)
    },
    { id: 'Data', label: '', minWidth: 100, maxWidth: 800,
      hasErrors: (value) => value?.includes('ErrorDescriptions')
    },

    // { id: 'State', label: '', minWidth: 100 },
    // { id: 'RasterState', label: '', minWidth: 100 },
    // { id: 'Position_Current', label: '', minWidth: 100 },
    // { id: 'ErrorDescriptions', label: '', minWidth: 100 },
  ];
  
export default function StandTable(props) {
  console.log("render StandTable");

  const rows = props.data;
  return (
    <CommonTable columns={columns} rows={rows}></CommonTable>
  );
}