import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import CommonTable from '../CommonTable'

const columns = [
    { id: 'EquipName', label: '', minWidth: 170 },
    { id: 'DateTime', label: '', minWidth: 100 },
    { id: 'SettingsDB', label: 'Settings ', minWidth: 100 },
    { id: 'ObservationsDB', label: 'Observations ', minWidth: 100 },
    { id: 'Version', label: ' ', minWidth: 100 },
    { id: 'XilibVersion', label: ' xilib', minWidth: 100 },
  ];

export default function DetectorTable(props) {
  console.log("render DetectorTable");

  const rows = props.data;
  return (
    <CommonTable columns={columns} rows={rows}></CommonTable>
  );
}